const initState = {
    isLoading: false
}
export type StateType = {
    isLoading:boolean
}
type ActionType = ReturnType<typeof loadingAC>
const LOADING = 'LOADING'
export const loadingReducer = (state:StateType = initState, action: ActionType): StateType => {
    switch (action.type) {
        case 'LOADING': {
            return {
                ...state,isLoading:action.isLoading
            }
        }
        default:
            return state
    }
}

export const loadingAC = (isLoading: boolean): {type:string, isLoading:boolean} => {
    return {type: LOADING, isLoading} as const
}