import React from 'react'
import SuperButton from '../h4/common/c2-SuperButton/SuperButton'
import {useDispatch, useSelector} from "react-redux";
import {AppStoreType} from "./bll/store";
import {loadingAC, StateType} from "./bll/loadingReducer";
import {Preloader} from "./Preloader";


function HW10() {
    const isLoading = useSelector<AppStoreType, StateType>(state => state.loading)
    const dispatch = useDispatch()
    const loading = isLoading.isLoading

    const setLoading = () => {
      dispatch(loadingAC(true))
        setTimeout(() => {
            dispatch(loadingAC(false))
        },3000)
        console.log('loading...')
    };

    return (
        <div>
            homeworks 10
            {loading
                ? (
                    <div><Preloader/></div>
                ) : (
                    <div>
                        <SuperButton title={'isLoad'} onClick={setLoading}>set loading...</SuperButton>
                    </div>
                )
            }

        </div>
    )
}

export default HW10
