import {userType} from "./tests/homeWorkReducer.test";

type ActionType =
    ReturnType<typeof sortArray> |
    ReturnType<typeof backSortArray> |
    ReturnType<typeof checkAdultUsers>

export const homeWorkReducer = (state: userType[], action: ActionType): userType[] => {
    switch (action.type) {
        case 'sort': {
            switch (action.payload) {
                case "up":
                    return state.slice().sort((a, b) => a.name > b.name ? 1 : -1)
                case "down":
                    return state.slice().sort((a, b) => a.name > b.name ? 1 : -1).reverse()
                default:
                    return state
            }
        }
        case 'check': {
            return state.filter(u => u.age >= action.payload)
        }
        default: return state
    }
}

export const sortArray = () => {
    return {type:'sort', payload: 'up'} as const
}
export const backSortArray = () => {
    return {type: 'sort', payload: 'down'} as const
}
export const checkAdultUsers = () => {
    return {type: 'check', payload: 18} as const
}
