import React, {useState} from 'react'
import SuperButton from '../h4/common/c2-SuperButton/SuperButton'

function Clock() {
    const [timerId, setTimerId] = useState<number>(0)
    const [date, setDate] = useState<Date>(new Date())
    const [show, setShow] = useState<boolean>(false)

    const stop = () => {
        clearTimeout(timerId)
    }
    const start = () => {
        stop()
        const id: number = window.setInterval(() => {
            return setDate(new Date())
            // return setDate(JSON.parse(JSON.stringify(new Date())))
        }, 1000)
        setTimerId(id)
    }

    const onMouseEnter = () => {
        setShow(true)
        // show
    }
    const onMouseLeave = () => {
        setShow(false)
    }

    const stringTime = new Date().toTimeString().replace(/ .*/, '')
    const stringDate = [new Date().getDate(), new Date().getMonth()+1, new Date().getFullYear()]
        .map(n => n < 10 ? `0${n}` : `${n}`).join('/')
const dateStyle = {
    backgroundColor:"#09149F",
    maxWidth: '90px',
    color: 'white',
    borderRadius: '8px',
    textAlign: 'center' as const,
    margin:'10px'
}
    return (
        <div >
            <div style={dateStyle}
                onMouseEnter={onMouseEnter}
                onMouseLeave={onMouseLeave}
            >
                {stringTime}
            </div>
            {show && (
                <div style={dateStyle}>
                    {stringDate}
                </div>
            )}

            <SuperButton title={'start'} onClick={start}/>
            <SuperButton title={'stop'} onClick={stop}/>

        </div>
    )
}

export default Clock
