import React from 'react'
import s from './Greeting.module.css'
import SuperInputText from "../h4/common/c1-SuperInputText/SuperInputText";
import SuperButton from "../h4/common/c2-SuperButton/SuperButton";


type GreetingPropsType = {
    name: string
    setNameCallback: (value:string) => void
    addUser: () => void
    error: string
    setError:(value:string) => void
    totalUsers: number
    addNameOnPress: () => void
    usersNamed:JSX.Element[]
}

// презентационная компонента (для верстальщика)
const Greeting: React.FC<GreetingPropsType> = (
    {name, setNameCallback, addNameOnPress, usersNamed, addUser, setError, error, totalUsers} // деструктуризация пропсов
) => {
    // const inputClass = (error !== '') ? s.error : s.someClass

    return (
        <div className={s.container}>
            <div>
                    <SuperInputText
                        value={name}
                        onChangeText={setNameCallback}
                        setError={setError}
                        // className={inputClass}
                        onEnter={addNameOnPress}
                        error={error}
                    />
                    {/*<input*/}
                    {/*    value={name}*/}
                    {/*    onChange={setNameCallback}*/}
                    {/*    className={inputClass}*/}
                    {/*    onKeyPress={addNameOnPress}*/}
                    {/*/>*/}
            </div>
                <div className={s.buttonAndSpan}>
                    <SuperButton onClick={addUser} title={'Add'}/>
                    <span className={s.counter}>{totalUsers}</span>
                </div>



            {/*    <div>*/}
            {/*        <span>{error}</span>*/}
            {/*</div>*/}

            <div className={s.listName}>
                <h3>List Name</h3>
               <ul>
                   {usersNamed}
               </ul>
            </div>
        </div>
    )
}

export default Greeting
